.app {
  display: flex;
}
.main {
  flex-basis: 60%;
  display: flex;
  flex-direction: column;
  /* background-color: rgba(121, 200, 232, 0.379); */
}
.title h1 {
text-align: center;
font-family: 'Courier New', Courier, monospace;
font-size: 40px;
margin: 1.5rem;
}
.title h3 {
  font-style: oblique;
  font-family: cursive;
  text-align: center;
  margin: 1rem;
}
.search {
  padding: 13px;
  margin-right: 13px;
}
.search input {
  width: 100%;
  height: 40px;
  font-size: 20px;
  color: blueviolet;
  padding-left: 5px;
  font-style: oblique;
}
.map {
  flex-grow: 1;
  height: 100vh;
  top: 0;
  position: sticky;
  /* background-color: red; */
}
.flats {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  font-family: monospace;
}
